const required = () => v => !!v
const email = () => v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v)
const minlen = compareTo => v => v && v.length >= compareTo
const equal = compareTo => v => v && v === compareTo

export {
  required,
  email,
  minlen,
  equal
}

export default {
  required,
  email,
  minlen,
  equal
}